.map {
    width: 90%;
    height: 90%;
}

svg.map {
    fill: #e3e3e3;
    stroke: #b0b0b0;
}

svg.map a:visited {
    fill: #e3e3e3;
}

svg.map a :hover {
    stroke-width: 5px;
    stroke-linejoin: round;
    stroke: black;
}

.info {
    display: none;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    /* You can adjust the number of columns as needed */
    gap: 10px;
    /* Adjust the gap between elements */
}

.grid-item {
    /* border: 1px solid #ccc; */
    padding: 5px;
    text-align: center;
}

p {
    text-align: justify;
    width: 80%;
    margin-left: 10%;
}

#panel {
    display: grid;
    grid-template-columns: 2fr 1fr;
    /* You can adjust the number of columns as needed */
    gap: 20px;
    /* Adjust the gap between elements */
}

header {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 10px;
}

/* Style the links */
header a {
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
}